import React, { Component } from 'react';
import { Col, Form, Row, InputNumber, Spin } from 'antd';
import { BtnSearch, BtnClear } from 'components/common/button';
import { SelectAjax } from 'components/common';

import { getTrainTypes } from 'services/trainType';
import { getFacultys } from 'services/faculty';
import { getCourses } from 'services/course';
import { getStudyFields } from 'services/StudyField';
import { browseClassNoGraduated, duplicateSchedule } from 'services/schedule';
import { getYearEducations } from 'services/yearEducationService';
import { getSemesters } from 'services/semesterService';

@Form.create({ name: 'FormDuplicatePlan' })
class FormSearch extends Component {
  constructor(props) {
    super(props);
    this.state = {
      loading: false,
    };
  }

  handleReset = () => {
    this.props.form.resetFields();
  };

  handleSearch = async (e) => {
    e.preventDefault();
    const { loading } = this.state;
    if (loading) {
      return;
    }
    await this.props.form.validateFields(async (err, values) => {
      if (err) {
        return;
      }
      try {
        this.setState({
          loading: true,
        });
        const paramSearch = values;
        if (paramSearch.classId && paramSearch.yearEduId && paramSearch.semesterId) {
          await duplicateSchedule(paramSearch);
          this.props.onOK();
        }
      } catch (error) {
        console.log(error);
      } finally {
        this.setState({
          loading: false,
        });
      }
    });
  };

  render() {
    const { loading } = this.state;
    const { getFieldDecorator, setFieldsValue, getFieldValue } = this.props.form;
    const trainTypeId = getFieldValue('trainTypeId');
    const facultyId = getFieldValue('facultyId');
    const courseId = getFieldValue('courseId');
    const studyFieldId = getFieldValue('studyFieldId');
    const yearEduId = getFieldValue('yearEduId');
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 6 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 18 },
      },
    };
    const optionFieldDecorator = {
      trainTypeId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Hệ!',
          },
        ],
      },
      facultyId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Khoa!',
          },
        ],
      },
      courseId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Khóa',
          },
        ],
      },
      studyFieldId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn CN',
          },
        ],
      },
      classId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Lớp',
          },
        ],
      },
      yearEduId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Năm học',
          },
        ],
      },
      semesterId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Học kỳ',
          },
        ],
      },
    };
    return (
      <Spin spinning={loading}>
        <Form className="form-inline-label" layout="vertical" onSubmit={this.handleSearch}>
          <Row gutter={24}>
            <Form.Item {...formItemLayout} label="Hệ">
              {getFieldDecorator('trainTypeId', optionFieldDecorator.trainTypeId)(
                <SelectAjax service={getTrainTypes} />
              )}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Khoa">
              {getFieldDecorator('facultyId', optionFieldDecorator.facultyId)(
                <SelectAjax
                  setFieldsValue={setFieldsValue}
                  service={getFacultys}
                  params={{ trainTypeId }}
                  allowGetData={!!trainTypeId}
                // open={!!trainTypeId && !facultyId}
                />
              )}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Khoá">
              {getFieldDecorator('courseId', optionFieldDecorator.courseId)(
                <SelectAjax
                  setFieldsValue={setFieldsValue}
                  service={getCourses}
                  params={{ trainTypeId, facultyId }}
                  allowGetData={!!facultyId}
                />
              )}
            </Form.Item>
            <Form.Item {...formItemLayout} label="CN">
              {getFieldDecorator('studyFieldId', optionFieldDecorator.studyFieldId)(
                <SelectAjax
                  service={getStudyFields}
                  setFieldsValue={setFieldsValue}
                  params={{ trainTypeId, facultyId, courseId }}
                  allowGetData={!!courseId}
                />
              )}
            </Form.Item>
            <Form.Item  {...formItemLayout} label="Lớp">
              {getFieldDecorator('classId', optionFieldDecorator.classId)(
                <SelectAjax
                  typeValue="int"
                  service={browseClassNoGraduated}
                  setFieldsValue={setFieldsValue}
                  params={{ trainTypeId, facultyId, courseId, studyFieldId }}
                  allowGetData={!!studyFieldId}
                  keyLabel="classLabel"
                  keyValue="classId"
                />
              )}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Năm học">
              {getFieldDecorator('yearEduId', optionFieldDecorator.yearEduId)(
                <SelectAjax
                  service={getYearEducations}
                  setLabel={yearEdu => `${yearEdu.fromYear}-${yearEdu.toYear}`}
                  typeValue="int"
                />
              )}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Học kì">
              {getFieldDecorator('semesterId', optionFieldDecorator.semesterId)(
                <SelectAjax
                  setFieldsValue={setFieldsValue}
                  service={getSemesters}
                  params={{ yearEduId }}
                  allowGetData={!!yearEduId}
                  typeValue="int"
                />
              )}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Từ tuần:">
              {getFieldDecorator(`fromWeek`, {})(
                <InputNumber min={1} max={50} defaultValue={1} style={{ width: '100%' }} />
              )}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Đến tuần:">
              {getFieldDecorator(`toWeek`, {})(
                <InputNumber min={1} max={50} defaultValue={1} style={{ width: '100%' }} />
              )}
            </Form.Item>
            <Col span={4}>
              <div className="btn-group">
                <BtnSearch htmlType="submit" />
                <BtnClear onClick={this.handleReset} />
              </div>
            </Col>
          </Row>
        </Form>
      </Spin>
    );
  }
}

export default FormSearch;



